import React from "react";
// import fade1 from "../Components/fade1";
import { animated, useSpring } from "react-spring";
import styled from "styled-components";
import { ListGroup } from "react-bootstrap";

export const About = () => {
  const yeet = useSpring({
    from: { opacity: 0 },
    to: { opacity: 1 },
    config: { duration: 1500 },
  });

  return (
    <animated.div style={yeet}>
      <h2> About page</h2>
      <p> very epic this is the about page</p>
      <p> This is a webpage that I created to learn react</p>
      <p> Implemented packages:</p>

      <ul> React: HashRouter, Route, Switch</ul>
      <ul> Styled-Components: styled</ul>
      <ul> React-Bootstrap: Container, Nav, Navbar</ul>
      <ul> React-Spring: animated, useSpring</ul>

      <Styles1>
        <ul className="list-group list-group-flush dark">
          <li className="list-group-item">Usingbootstrapcss</li>
          <li className="list-group-item">Dapibus ac facilisis in</li>
          <li className="list-group-item">Morbi leo risus</li>
          <li className="list-group-item">Porta ac consectetur ac</li>
          <li className="list-group-item">Vestibulum at eros</li>
        </ul>
      </Styles1>
      <br></br>
      <ListGroup>
        <ListGroup.Item>Using ReactBootstrap</ListGroup.Item>
        <ListGroup.Item
          action
          variant="dark"
          onClick={asdf()}
          href="#/about/#move1"
        >
          refresh, on click aint working ;/
        </ListGroup.Item>
        <ListGroup.Item>asdf</ListGroup.Item>
      </ListGroup>
    </animated.div>
  );
};
function asdf() {
  console.log("asdf");
}
const Styles1 = styled.div`
  .list-group .list-group-flush {
    background-color: blue;
    color: black;
  }

  .list-group-item {
    background-color: #f1f9fb;
    border-top: 1px solid #0091b5;
    border-left-color: #fff;
    border-right-color: #fff;
  }
`;
