import React from "react";
//import logo from "./logo.svg";
import "./App.css";
import { Route, Switch } from "react-router-dom";
import { Home } from "./Pages/Home";
import { About } from "./Pages/About";
import { Contact } from "./Pages/Contact"; //add the curly brackets around home, about, contact b/c there is no default export stated
import Layout from "./Components/Layout";
import { NavBar1 } from "./Components/NavBar1";
// import { Spring } from 'react-spring';
import { useSpring, animated, config } from "react-spring";
// import fade1 from "./Components/fade1";
import { Carousel1 } from "./Components/Carousel1";

function App() {
  const colourChange = useSpring({
    from: { opacity: 0, color: "red" },
    to: [
      { opacity: 1, color: "#ffaaee" },
      { opacity: 1, color: "red" },
      { opacity: 0.5, color: "#008000" },
      { opacity: 0.8, color: "black" },
      { opacity: 0.95, color: "aqua" },
    ],
    config: {
      mass: 5,
      tension: 50,
      friction: 25,
      clamp: true,
    },
  });

  const fade = useSpring({
    from: { opacity: 0 },
    to: { opacity: 1 },
    config: { duration: 2000 },
  });
  //more mass = slows down further (acts like heavy)
  //tension speeds up?
  //more friction slows down

  return (
    <animated.div style={fade} className="border1">
      <div className="App">
        <header className="App-header">
          <img
            src="https://i.imgur.com/6Nn4ucD.gif"
            className="App-logo"
            alt="logo"
          />
          <animated.p style={colourChange}>
            Offically Deployed on Github-Pages yawyeet <code>using react</code>{" "}
            IT SPINS REEE.
          </animated.p>
        </header>
      </div>

      <Carousel1 />

      {/* <a href="#move1"> */}
      <NavBar1 />
      {/* </a> */}
      <Layout>
        <section id="move1"></section>
        <animated.div style={fade} className="blank">
          <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/about" component={About} />
            <Route path="/contact" component={Contact} />
            <Route component={() => <div>404 Not found reeee</div>} />
          </Switch>
        </animated.div>
      </Layout>
    </animated.div>
  );
}
export default App;

